<template>
    <div class="login">
        <div class="login-header" :style="{background:skinColor}">
            <i @click="hiddenLogin" class="fa fa-chevron-left" aria-hidden="true"></i>
        </div>

        <div class="login-input">
            <div class="text">
                <input v-model="accessToken" type="text" placeholder="Access Token">
                <span class="textBottom"></span>
            </div>
            <Buttons @click="login(accessToken)" text="" :style="{background:skinColor}"></Buttons>
         </div>

        <div class="login-tips">
            <ul class="tips">
                <li class="tip"><br class="br"/><b>3</b></li>
                <li class="tip"><a class="title" href="https://cnodejs.org">CNode</a>CNode</li>
                <li class="tip"><a class="title" href="https://cnodejs.org/setting"></a>CNodeAccess Token</li>
                <li class="tip"><img class="accessTokenImg" src="../../assets/img/Access Token.jpg" alt="CNodeAccess Token"></li>
            </ul>
        </div>
  </div>
</template>

<script>
    import './Login.scss'
    import Login from './Login.js'
    export default Login
</script>


