

export default {
    name: 'About',
    data() {
        return {
            skills:[
             {name: 'Vue2.2', des: ''},
             {name: 'Vuex', des: ','}, 
             {name: 'Vue-router', des: ''},
             {name: 'ES6/7', des: 'JS'},
             {name: 'Webpack', des: ''},
             {name: 'Express', des: ''},
             {name: 'Axios', des: 'PromiseHTTP'},
             {name: 'SASS', des: 'css'},
             {name: 'Normalize.css', des: ''},
             {name: 'Font-awesome', des: ''},
             {name: 'Moment.js', des: ''},
             {name: 'Simplemde', des: 'markdown'},
             {name: 'Highlight.js', des: ''},
             {name: 'Flex', des: ''},
             {name: 'loaclStorge', des: ''}
            ],
            skillIndex: 0,
        }
    },
    computed: {
        skinColor() {
            return this.$store.state.skinColor;
        },
        at() {
            return this.$store.state.at;
        }
    },
    methods: {
        hiddenAbout() {
            this.$router.go(-1);
        },
        skillClick(idx) {
            this.skillIndex = idx;
        }
    }
}


