import Reply from '../Reply/Reply.vue'
import axios from 'axios'
import Buttons from '../button/button.vue'
import bus from '../../lib/bus.js'

export default {
    name: 'NewArticle',
    data() {
        return {
            types: { 
                share: '',
                ask: '',
                job: '',
                dev: ''
            },
            newArticleType: '',
            type: 'dev',
            newArticleTitle: '',

        }
    },
    computed: {
        skinColor() {
            return this.$store.state.skinColor;
        },
        at() {
            return this.$store.state.at;
        },
    },
    created() {
        bus.$on('reply', (mde) => {
            this.sendNewArticle(mde);
        })
    },
    components: {
        Reply,
        Buttons
    },
    methods: {
        hiddenNewArticle()  {
            this.$router.go(-1);
        },
        sendNewArticle(mde) {
            console.log(mde.value());
            if(!this.newArticleTitle.match('.{10,}')) {
                console.log('');
                return;
            }
            if(!mde.value()) {
                console.log('');
                return;
            }
            for(let type in this.types) {
                if(this.newArticleType === this.types[type]) {
                    this.type = type;
                }
            }
             console.log(this.type);
             axios.post(`https://cnodejs.org/api/v1/topics`, {
                 accesstoken: this.at,
                 title: this.newArticleTitle,
                 tab: this.type,
                 content: mde.value()
             })
             .then(result => {
                 if (result.data && result.data.success) {
                      this.$store.commit('showAsideMenu', false);
                      this.$store.commit('changeTab', this.type)
                      this.$router.push({name: 'Article', params: {id: result.data.topic_id}});
                } else {
                    console.log('');
                }
             })
            

        }

    }
}


